import { supabase } from "./database.config";

class Storage {
  /**
   *  –î–æ–±–∞–≤–ª–µ–Ω–∏–µ —Ñ–æ—Ç–æ–≥—Ä–∞—Ñ–∏–∏ –≤ —Ö—Ä–∞–Ω–∏–ª–∏—â–µ
   * @param {number} id –ò–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
   * @param {object} file –û–±—å–µ–∫—Ç —Ñ–æ—Ç–æ–≥—Ä–∞—Ñ–∏–∏
   * @returns –ò–º—è –∫–∞—Ä—Ç–∏–Ω–∫–∏ —Å —Ä–∞—Å—à–∏—Ä–µ–Ω–∏–µ–º
   */
  async setAvatar(id, file) {
    try {
      if (!file) throw new Error("Avatar is not defined");

      const fileExt = file.name.split(".").pop();
      const filePath = `${id}.${fileExt}`;

      const { data, error } = await supabase.storage
        .from("mentors")
        .upload(filePath, file);

      if (error) throw new Error(error);
      return data.path;
    } catch (error) {
      return error;
    }
  }
  /**
   * –ü–æ–ª—É—á–µ–Ω–∏–µ –ø—É–±–ª–∏—á–Ω–æ–π —Å—Å—ã–ª–∫–∏ –Ω–∞ —Ñ–æ—Ç–æ–≥—Ä–∞—Ñ–∏—é
   * @param {string} filename –ò–º—è –∫–∞—Ä—Ç–∏–Ω–∫–∏ —Å —Ä–∞—Å—à–∏—Ä–µ–Ω–∏–µ–º
   * @returns –ü—É–±–ª–∏—á–Ω–∞—è —Å—Å—ã–ª–∫–∞ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É
   */
  getAvatar(filename) {
    try {
      const { data, error } = supabase.storage
        .from("mentors")
        .getPublicUrl(filename);

      if (error) throw new Error(error);

      return data.publicUrl;
    } catch (error) {
      return error;
    }
  }
}

export default new Storage();
